<template>
    <div class="search-result">
        <ul>
            <li v-for="item in books" :key="item._id" @click="toBookInfo(item._id)">
                <div class="cover">
                    <img :src="fixImg(item.cover)" />
                </div>
                <div class="msg">
                    <h1>{{item.title}}</h1>
                    <p class="textOverflow2">{{item.shortIntro}}</p>
                    <div class="tags">
                        <div>
                            <van-icon name="manager" /> {{item.author}}
                            <span class="follower">{{item.latelyFollower}}人气</span>
                        </div>
                        <div>
                            <van-tag round text-color="#999" color="#efefef">{{item.cat}}</van-tag>&nbsp;
                            <van-tag round text-color="#999" color="#efefef">{{item.hasCp?"完结":""}}</van-tag>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { fetchSearchResult } from '@/api/searching'
export default {
    name: 'SearchResult',
    data() {
        return {
            keyword: this.$route.query.keyword,
            books: [],
        }
    },
    created () {
        this.getSearchResult()
    },
    methods: {
        getSearchResult() {
            fetchSearchResult(this.keyword).then(res => {
                this.books = res.data.books
            })
        },
        fixImg(list) {
            return decodeURIComponent(list).slice(7)
        },
        toBookInfo(id) {
            this.$router.push({ name: 'BookInfo', params: { id: `${id}` }})
        }
    }
}
</script>

<style lang="scss" scoped>
.search-result {
    padding: 10px;
    ul {
        li {
            display: flex;
            justify-content: start;
            padding-bottom: 15px;
            .cover {
                img {
                    width: 75px;
                    height: 100px;
                }
            }
            .msg {
                width: calc(100% - 75px);
                padding: 5px 0 5px 10px;
                h1 {
                    font-size: 16px;
                    font-weight: 700;
                    color: #000;
                }
                p {
                    margin: 10px 0;
                    color: #999;
                    line-height: 20px;
                }
                .tags {
                    display: flex;
                    justify-content: space-between;
                    color: #999;
                    .follower {
                        color: red;
                        padding-left: 10px;
                    }
                }

            }
        }
    }
}
</style>