import request from '@/utils/request'

// 热门搜索
export function fetchHotBooks() {
    return request({
      url: '/hot-books',
      method: 'get'
    })
}
// 获取搜索结果
export function fetchSearchResult(keyword) {
    return request({
      url: '/search',
      method: 'get',
      params: { keyword }
    })
}